
import UserItem from '@/components/UserItem.vue'
import AppTitle from '@/components/UI/AppTitle.vue'
import useUserId from '@/hooks/useUserId'

export default {
    components: {
        UserItem,
        AppTitle
    },

    props: {
        users: {
            type: Array,
            required: true
        },
        searchedUsers: {
            type: Array,
            required: true
        },
        loadingData: {
            type: Boolean,
            required: true
        },
        searchQuery: {
            type: Array,
            required: true
        }
    },

    setup () {
        const { changeUserId } = useUserId()

        const handleClick = (userId: number) => changeUserId(userId)

        return {
            changeUserId,
            handleClick
        }
    }
}
